import * as React from 'react';
import styled from 'styled-components';
export function ListElement(props: {
  id: string;
  payload: string;
}) {

  // Styles for the table
  const TableStyle = styled.table`
    font-family:Arial, sans-serif;
    border-radius: 25px;
    border-collapse:collapse;
    border-spacing:0;
    margin: 1rem;
    min-width:400px;
    width: 400px;
    max-width: 400px;
    flex:1;
  `;

  const TableHeaderStyle = styled.th.attrs({
    colSpan: 2
  })`
    background-color:#393b44;
    color: #f1f3f8;
    font-size:14px;
    font-weight:bold;
    overflow:hidden;
    padding:10px 5px;
    word-break:break-all;
  `;

  const TableCellNameStyle = styled.td`
    border-bottom: 1px solid #8d93ab;
    background-color:#d6e0f0;
    color: #393b44;
    font-size:14px;
    overflow:hidden;
    padding:10px 5px;
    word-break:break-all;
    min-width:130px;
  `;

  const TableCellValueStyle = styled.td`
    border-bottom: 1px solid #8d93ab;
    color: #393b44;
    font-family:Arial, sans-serif;
    font-size:14px;
    overflow:hidden;
    padding:10px 5px;
    word-break:break-all;
  `;

  // Parse and format the date
  function parseDate(date: string) {
    const SplitDate = (
      (date.indexOf('T') > -1) ?
        date.split('T', 2) :
        date.split(' ', 2)
    );
    // Destructuring the splitted arrays
    const [Year, Month, Day] = SplitDate[0].split('-', 3);
    const [Hour, Min, Sec] = SplitDate[1].split(':', 3);
    const FormattedDate = `${Year}/${Month}/${Day} ${Hour}:${Min}:${Sec.substring(0, 2)}`;
    return FormattedDate;
  }

  function displayAttributes(data: string) {
    let structure: JSX.Element[] = [];
    // Parse the payload to JSON object
    const payload = JSON.parse(data);
    // Go through every attribute in the payload.
    Object.keys(payload).forEach(key => {
      if (typeof payload[key] !== 'object') {
        const row = (
          <tr key={key}>
            <TableCellNameStyle>
              {key}
            </TableCellNameStyle>
            <TableCellValueStyle>
              {
                (typeof payload[key] !== 'string') ? JSON.stringify(payload[key]) :
                  (payload[key] === null || payload[key] === '') ? 'Empty' :
                    (key === 'timestamp') ? parseDate(payload[key]) :
                      payload[key]
              }
            </TableCellValueStyle>
          </tr>
        );
        structure.push(row);
      }
    });
    return structure;
  }

  return (
    <TableStyle>
      <thead>
        <tr>
          <TableHeaderStyle>{props.id}</TableHeaderStyle>
        </tr>
      </thead>
      <tbody>
        {displayAttributes(props.payload)}
      </tbody>
    </TableStyle >
  );
}


