import * as React from 'react';
import { createGlobalStyle } from 'styled-components';
import { ListElement } from '@App/components/app/ListElement';
import { RootState } from '@App/store/reducers';
import { connect } from 'react-redux';
import { Dispatch } from 'redux';
import { Observation } from '@App/types/Observation';
import styled from 'styled-components';
import ReactPaginate from 'react-paginate';
import '@App/components/app/App.css';

interface AppProps {
}

interface AppState {
  observations: Observation[];
  pageObservations: Observation[];
  offset: number;
  elementsPerPage: number;
  currentPage: number;
  pageCount: number;
}

const GlobalStyle = createGlobalStyle`
  body {
    height: 100vh;
    background-color: #F9F9F9;
    > div {
      height: 100%;
    }
  }
`;

const UnorganizedList = styled.ul`
  list-style: none;
  margin: 10px;
  padding: 10px;
  display:flex;
  flex-wrap: wrap;
`;

class App extends React.Component<AppProps, AppState> {
  state: AppState = {
    observations: [],
    pageObservations: [],
    offset: 0,
    elementsPerPage: 12,
    currentPage: 0,
    pageCount: 0,
  };

  public constructor(props: AppProps) {
    super(props);
    // Bind the context
    this.handlePageChange = this.handlePageChange.bind(this);
    this.setPageElements = this.setPageElements.bind(this);
  }

  // Set the elements shown in each page using the current page, and how many items to show
  public setPageElements() {
    let elements = this.state.observations.slice(
      this.state.offset,
      this.state.offset + this.state.elementsPerPage
    );
    this.setState({ pageObservations: elements });
  }

  componentDidMount() {
    fetch('/data')
      .then(res => res.json())
      .then(
        res => {
          this.setState(
            {
              observations: res.data,
              pageCount: (res.data.length / this.state.elementsPerPage)
            }
          );
          this.setPageElements();
        }
      );
  }

  public handlePageChange(data: { selected: number; }) {
    const selectedPage = data.selected;
    const offset = selectedPage * this.state.elementsPerPage;
    this.setState({ currentPage: selectedPage, offset: offset });
    this.setPageElements();
  }

  public render() {
    let paginationElement;
    if (this.state.pageCount > 1) {
      paginationElement = (
        <ReactPaginate
          previousLabel={'<'}
          nextLabel={'>'}
          breakLabel={<span>...</span>}
          pageCount={this.state.pageCount}
          onPageChange={this.handlePageChange}
          forcePage={this.state.currentPage}
          pageRangeDisplayed={2}
          marginPagesDisplayed={2}
          containerClassName={'pagination'}
          previousLinkClassName={'previous_page'}
          nextLinkClassName={'next_page'}
          disabledClassName={'disabled'}
          activeClassName={'active'}
        />
      );
    }

    return (
      <>
        <GlobalStyle />
        <div className="App-intro">
          {paginationElement}
          <UnorganizedList>
            {
              this.state.pageObservations.map(
                table =>
                  <ListElement
                    key={table.id}
                    id={table.id}
                    payload={table.payload}
                  />
              )
            }
          </UnorganizedList>
          {paginationElement}
        </div>
      </>
    );
  }
}

const mapStateToProps = (state: RootState, ownProps: object) => { };

const mapDispatchToProps = (dispatch: Dispatch<RootState>) => { };

export default connect(mapStateToProps, mapDispatchToProps)(App);


